.contact__container{
    width: 100vw;
    height: 100vh;
    background: #000;
    color: #fff;
    display: grid;
    grid-template-columns: 50% 50%;
    text-align: center;
    align-items: center;
}

.contact__grid-item:nth-child(1){
    display: grid;
    grid-template-rows: 50% 50%;
    font-size: 1.5rem;
}

.contact__grid-item:nth-child(2){
    margin-top: -8vh;
    font-size: 1.5rem;
}

.contact__address-head{
    margin-top: 5vh;
    font-weight: 800;
    margin-bottom: 5vh;
    letter-spacing: 0.2rem;
}

.contact__hotline-head{
    letter-spacing: 0.3rem;
    font-weight: 800;
}

.contact__image{
    margin: 8vh;
    width: 40vw;
}

@media screen and (max-width: 1024px){
    .contact__container{
        grid-template-columns: 0 0;
        grid-template-rows: 50% 50%;
        height: 190vh;
    }

    .contact__grid-item{
        width: 100vw;
    }

    .contact__grid-item:nth-child(1){
        margin-top: 170vh;
    }

    .contact__image{
        width: 60vw;
    }

    .contact__hotline-head{
        font-size: 1.3rem;
    }
}