.partners__container{
    width: 100vw;
    height: 100vh;
    display: grid;
    grid-template-rows: auto auto auto;
    justify-content: center;
    align-items: center;
    background: #000;
    color: #fff;
}

.partners__image{
    width: 25vw;
    height: 43vh;
    transform: scale(0.8);
    transition: all 0.3s ease-out;
}

.partners__image:hover{
    cursor: pointer;
    transform: scale(0.9);
}

.partners__grid-item:nth-child(1){
    display: flex;
    justify-content: center;
}
.partners__grid-item:nth-child(3){
    display: flex;
    justify-content: center;
}

.partners-top{
    margin-top: 5vh;
    margin-left: -2vw;
    margin-right: -2vw;
    margin-bottom: -18vh;
}

.partners-middle{
    margin-left: -2vw;
    margin-right: -2vw;
}

.partners-bottom{
    margin-top: -18vh;
    margin-left: -2vw;
    margin-right: -2vw;
}

@media screen and (max-width: 1024px){
    .partners__grid-item{
        display: flex;
        justify-content: center;
        width: 100vw;
    }

    .partners__grid-item:nth-child(1){
        margin-top: 12vh;
        margin-bottom: -12vh;
    }

    .partners__grid-item:nth-child(3){
        margin-top: -12vh;
        margin-bottom: 12vh;
    }

    .partners__image{
        width: 45vw;
        height: 25vh;
    }

    .partners-top{
        margin-left: -8vw;
        margin-right: -8vw;
    }

    .partners-middle{
        margin-left: -15vw;
        margin-right: -15vw;
    }

    .partners-bottom{
        margin-left: -8vw;
        margin-right: -8vw;
    }
}

@media screen and (width: 1024px){
    .partners__image{
        height: 35vh;
    }

    .partners__grid-item:nth-child(1){
        margin-top: 20vh;
        margin-bottom: -10vh;
    }

    .partners__grid-item:nth-child(3){
        margin-top: -10vh;
        margin-bottom: 10vh;
    }
    

    .partners-top{
        margin-top: -12vh;
        margin-left: -9vw;
        margin-right: -9vw;
        margin-bottom: -14vh;
    }
    
    .partners-middle{
        margin-left: -18vw;
        margin-right: -18vw;
    }

    .partners-bottom{
        margin-top: -14vh;
        margin-left: -9vw;
        margin-right: -9vw;
    }
}