.landing__container{
    overflow: hidden;
    position: absolute;
    z-index: 9999;
    display: grid;
    grid-template-rows: 80% 20% auto;
    height: 100vh;
    width: 100vw;
    background: url(/static/media/home-background.942788f5.jpg);
    background-size: cover;
    background-repeat: no-repeat;
    justify-content: center;
    align-items: center;
    text-align: center;
    color: #fff;
    animation: slide-up 5s ease;
    animation-fill-mode: forwards;
}

@keyframes slide-up{
    0%{
        opacity: 0;
        transform: translateY(0);
    }
    5%{
        opacity: 1;
    }
    50%{
        transform: translateY(0);
    }
    100%{
        transform: translateY(-100vh);
    }
}

.landing__logo{
    width: 30vw;
    height: 60vh;
}

.landing__logo-content{
    font-size: 3rem;
    letter-spacing: 0.4rem;
}

.landing__dot-animation{
    margin-top: -10vh;
    font-size: 10rem;
}

.landing__dot-animation span {
    font-family: 'Quicksand';
    animation-name: blink;
    animation-duration: 1.4s;
    animation-iteration-count: infinite;
    animation-fill-mode: both;
}
  
.landing__dot-animation #dot_2 {
    animation-delay: .2s;
}
  
.landing__dot-animation #dot_3 {
    animation-delay: .4s;
}
.landing__dot-animation #dot_4 {
    animation-delay: .6s;
}
.landing__dot-animation #dot_5 {
    animation-delay: .8s;
}

  
@keyframes blink {

    0% {
      opacity: .2;
    }

    20% {
      opacity: 1;
    }

    100% {
      opacity: .2;
    }
  }

  ::-webkit-scrollbar{
      width: 0;
  }

  @media screen and (max-width: 1024px){

    .landing__logo{
        width: 70vw;
        height: 70vw;
    }

    .landing__logo-content{
          font-size: 2rem;
    }

    .landing__bottom-text{
        font-size: 1.4rem;
    }

    .landing__dot-animation{
        font-size: 8rem;
    }
  }

  @media screen and (width: 1024px){
      .landing__dot-animation{
          padding-top: 6vh;
      }
  }
.navbar{
    display: flex;
    width: 100vw;
    height: 10vh;
    background: #000;
    position: fixed;
    opacity: 0;
    top: 0;
    animation: zoomIn 3s ease;
    animation-fill-mode: forwards;
    z-index: 999;
}

@keyframes zoomIn {
    0%{
        opacity: 0;
    }
    100%{
        opacity: 1;
    }
}

.navbar__container{
    position: fixed;
    display: flex;
    background: #000;
    color: #fff;
    justify-content: center;
    text-align: center;
    width: 100vw;
    font-size: 1.2rem;
    z-index: 999;
}

.navbar-logo {
    color: #ffd000;
    margin: 10px;
    width: 4vw;
    height: 55px;
    justify-self: start;
    cursor: pointer;
    text-decoration: none;
    display: flex;
    align-items: center;
}

.logo-icon{
    width: 2vw;
}

.nav-links{
    display: flex;
    align-items: center;
    text-decoration: none;
    padding: 0.5rem 1rem;
    height: 100%;
    width: -webkit-max-content;
    width: -moz-max-content;
    width: max-content;
    color: #fff;
    font-weight: 600;
}

.menu-icon {
    display: none;
  }

.nav-item {
    align-items: center;
    margin: 0 2.5vw 0 2.5vw;
    height: -webkit-max-content;
    height: -moz-max-content;
    height: max-content;
    border-bottom: 4px solid transparent;
    transition: all 0.3s ease-out;
    cursor: pointer;
 }

.navbar-icon {
    margin-right: 0.5rem;
}
  
.nav-menu {
    display: flex;
    align-items: center;
    justify-content: center;
    list-style: none;
    text-align: center;
    justify-content: end;
}

.nav-item:hover {
    border-bottom: 4px solid #f00946;
}

@media screen and (max-width: 1024px) {

    .navbar{
        background: #000;
    }

    .nav-item{
        margin-top: 4vh;
    }

    .navbar-logo{
        position: absolute;
        left: 2vw;
        width: 5vw;
        z-index: 999;
    }

    .nav-menu{
        display: flex;
        flex-direction: column;
        height: 100vh;
        opacity: 1;
        transition: all 0.5s ease;
        position: absolute;
        left: -100%;
        width: 100%;
    }

    .nav-menu.active{
        background: #000;
        left: 0;
        opacity: 1;
        z-index: 1;
    }

    .menu-icon{
        position: absolute;
        z-index: 999;
        font-size: 4rem;
        width: 6vw;
        margin: 10px;
        right: 0;
        height: 55px;
        justify-self: start;
        cursor: pointer;
        text-decoration: none;
        display: flex;
        align-items: center;
    }

    .nav-item:nth-child(1){
        margin-top: 15vh;
    }

    .logo-icon{
        width: 6vw;
    }

}

@media screen and (width: 1024px){
    .menu-icon , .navbar-logo{
        margin: 3rem;
    }
}


