    justify-content: center;
}

.ns__post{
    height: -webkit-max-content;
    height: -moz-max-content;
    height: max-content;
    margin: 5vh 5vw 2vw 5vw;
    border-radius: 0.5rem;
    justify-self: center;
    justify-content: center;
    text-align: center;
}

