    align-items: center;
    font-size: 1.5rem;
    width: 15vw;
    height: 5vh;
    background: #fff;
    color: #000;
}

.ns__overlay{
    position: absolute;
    background: #000;
    opacity: 0;
    width: 15vw;
    height: 40vh;
    transition: all 0.3s ease;
}

.ns__overlay-text-wrapper{
    display: grid;
    grid-template-rows: 20% 20% 20% 20%;
    font-size: 2rem;
    font-weight: 600;
    opacity: 1;
}

