.services__container{
    overflow-x: hidden;
    width: 100vw;
    height: 100vh;
    display: grid;
    grid-template-rows: 8% 92%;
    background: #000;
    color: #fff;
    text-align: center;
    overflow-y: hidden;
    transition: all 0.4s ease;
    text-align: center;
}

.grid-item{
    display: grid;
    grid-template-columns: 50% 50%;
}

.services__grid-item:nth-child(1){
    display: flex;
    align-content: center;
    justify-content: center;
    margin-top: 10vh;
    order: 2;
}

.services__grid-item:nth-child(2){
    order: 1;
    display: grid;
    grid-template-columns: 50% 50%;
    margin-top: 10vh;
    margin-left: 3vw;
}

.services__heading{
    font-size: 3rem;
    margin-top: 12vh;
}

.services__desc-wrapper{
    display: flex;
    margin: 9vh;
    justify-content: center;
    align-items: center;
    font-size: 1.3rem;
    text-align: center;
}

.services__wrapper-grid-item{
    height: max-content;
}

.services__text-wrapper{
    margin-top: 5vh;
    display: flex;
    font-size: 1.5rem;
    cursor: pointer;
}

.services__num{
    font-size: 4rem;
    color: rgb(112, 112, 112);
    font-weight: bold;
}

.services__text{
    width: 20vw;
    justify-content: center;
    font-size: 1.6rem;
    padding-top: 1vh;
    padding-left: 2vw;
}

.services__image{
    margin-top: 10vh;
    width: 30vw;
}

@media screen and (max-width: 1024px){

    .services__container{
        height: 100%;
    }

    .grid-item{
        width: 100vw;
        display: flex;
        flex-direction: column;
    }

    .services__container{
        display: flex;
        flex-direction: column;
    }

    .services__grid-item:nth-child(1){
        margin-top: 0;
        order: 1;
    }

    .services__grid-item:nth-child(2){
        margin-top: 0vh;
        height: 30vh;
        justify-content: center;
        order: 2;
    }

    .services__text-wrapper{
        width: max-content;
        margin-top: 3vh;
    }

    .services__wrapper-grid-item{
        margin-top: -8vw;
        margin-left: 3vw;
        margin-right: 3vw;
    }

    .services__heading{
        font-size: 3rem;
    }

    .services__num{
        font-size: 1rem;
    }

    .services__text{
        margin-top: -1.5ch;
        font-size: 0.8rem;
        padding-top: 2vh;
        width: max-content;
    }

    .services__desc-wrapper{
        font-size: 0.7rem;
    }

    .services__image{
        width: 50vw;
    }
}

@media screen and (width: 1024px){

    .services__grid-item:nth-child(2){
        margin-top: 5vh;
    }

    .services__heading{
        font-size: 4rem;
    }

    .services__num{
        font-size: 3rem;
    }

    .services__text{
        margin-top: -1.5ch;
        font-size: 2rem;
    }

    .services__desc-wrapper{
        margin-top: 1vh;
        font-size: 1.2rem;
    }

    .services__wrapper-grid-item{
        margin-top: -8vh;
    }
}