body{
    overflow-x: hidden;
}

.home__container{
    display: grid;
    grid-template-columns: 50% 50%;
    width: 100vw;
    height: 100vh;
    background: #000;
    color: #fff;
}

.home__grid-item:nth-child(2){
    display: grid;
    grid-template-columns: 90% 10%;
}

.home__grid-item{
    text-align: center;
    justify-content: center;
    margin: 3vw;
    margin-top: 16vh;
}

.home__image{
    margin: 1vw;
    width: 20vw;
    height: 20vh;
}

.home__carousel-wrapper{
    border-radius: 2rem;
    height: 10vh;
    width: 45vw;
}

.home__carousel-image{
    border-radius: 2rem;
    height: 24vh;
    width: 45vw;
}

.home__carousel-overlay{
    position: absolute;
    display: grid;
    align-items: center;
    width: 45vw;
    height: 24vh;
    z-index: 1;
    background: #000;
    opacity: 0.5;
    color: #fff;
    font-size: 2rem;
}

.overlay-text{
    position: sticky;
    margin-right: 6vw;
    font-size: 4rem;
    color: #fff;
    font-weight: 700;
}


.home__youtube{
    width: 40vw;
    height: 50vh;
}  

.home__social-media{
    margin-bottom: 3vh;
    font-size: 2rem;
}

.home__social-media-wrapper{
    text-decoration: none;
    list-style: none;
}

.social-media-logo{
    margin-left: 1VW;
    width: 2vw;
}

@media screen and (max-width: 1024px){

    .home__container{
        height: 100%;
       display: flex;
       flex-direction: column;
    }

    .home__grid-item{
        margin: 5vw;
        width: 90vw;
    }

    .home__grid-item:nth-child(1){
        margin-top: 10vh;
    }

    .home__grid-item:nth-child(2){
        display: grid;
        grid-template-columns: 92% 8%;
    }

    .home__image{
        text-align: center;
        width: 75vw;
    }

    .home__carousel-image{
        margin-top: 5vh;
        width: 10vw;
    }

    .home__carousel-wrapper{
        height: 20vh;
        width: 10vw;
    }

    .home__carousel-overlay{
        margin-top: 5vh;
        width: 95vw;
    }

    .home__youtube{
        width: 70vw;
        height: 30vh;
    }

    .home__image{
        margin: 6vh 0 6vh 0;
    }

    .social-media-logo{
        width: 4vw;
    }

    .overlay-text{
        font-size: 2.5rem;
    }
}

@media screen and (width: 1024px){
    .home__grid-item:nth-child(2){
        margin-left: 5vw;
    }


    .social-media-logo{
        margin-top: 1vh;
        margin-left: 1VW;
        width: 4vw;
    }
}