@import url('https://fonts.googleapis.com/css2?family=Alegreya+Sans+SC:wght@300&display=swap');

html{
    background: #000;
}

*{
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    font-family: 'Alegreya Sans SC', sans-serif;
}

::-webkit-scrollbar{
    width: 12px     ;
    background: rgb(65, 65, 65);
}

::-webkit-scrollbar-thumb{
    background: #fff;
    border-radius: 12px;
}