.tutorup__container{
    background: #000;
    color: #fff;
    width: 100vw;
    height: 100vh;
    display: grid;
    grid-template-rows: 40% 10% 10% 10% 10% 10% 10%;
    justify-content: center;
    font-size: 1.5rem;
}

.tutorup__grid-item{
    display: flex;
    flex-direction: column;
    margin-top: 5vh;
}

.tutorup__grid-item:nth-child(1){
    margin-top: 10vh;
}

.tutorup__submit{
    color: #fff;
    font-weight: 600;
    background: #6495ed;
    border-radius: 0.5rem;
    font-size: 2rem;
}

.tutorup__submit:hover{
    cursor: pointer;
}

.tutorup__text-input{
    border-radius: 0.5rem;
    font-size: 1.3rem;
}

.tutorup__image-input:hover {
    cursor: pointer;
}

@media screen and (max-width:1024px){
    .tutorup__container{
        height: 100%;
    }
}