.landing__container{
    overflow: hidden;
    position: absolute;
    z-index: 9999;
    display: grid;
    grid-template-rows: 80% 20% auto;
    height: 100vh;
    width: 100vw;
    background: url('../../images/home-background.jpg');
    background-size: cover;
    background-repeat: no-repeat;
    justify-content: center;
    align-items: center;
    text-align: center;
    color: #fff;
    animation: slide-up 5s ease;
    animation-fill-mode: forwards;
}

@keyframes slide-up{
    0%{
        opacity: 0;
        transform: translateY(0);
    }
    5%{
        opacity: 1;
    }
    50%{
        transform: translateY(0);
    }
    100%{
        transform: translateY(-100vh);
    }
}

.landing__logo{
    width: 30vw;
    height: 60vh;
}

.landing__logo-content{
    font-size: 3rem;
    letter-spacing: 0.4rem;
}

.landing__dot-animation{
    margin-top: -10vh;
    font-size: 10rem;
}

.landing__dot-animation span {
    font-family: 'Quicksand';
    animation-name: blink;
    animation-duration: 1.4s;
    animation-iteration-count: infinite;
    animation-fill-mode: both;
}
  
.landing__dot-animation #dot_2 {
    animation-delay: .2s;
}
  
.landing__dot-animation #dot_3 {
    animation-delay: .4s;
}
.landing__dot-animation #dot_4 {
    animation-delay: .6s;
}
.landing__dot-animation #dot_5 {
    animation-delay: .8s;
}

  
@keyframes blink {

    0% {
      opacity: .2;
    }

    20% {
      opacity: 1;
    }

    100% {
      opacity: .2;
    }
  }

  ::-webkit-scrollbar{
      width: 0;
  }

  @media screen and (max-width: 1024px){

    .landing__logo{
        width: 70vw;
        height: 70vw;
    }

    .landing__logo-content{
          font-size: 2rem;
    }

    .landing__bottom-text{
        font-size: 1.4rem;
    }

    .landing__dot-animation{
        font-size: 8rem;
    }
  }

  @media screen and (width: 1024px){
      .landing__dot-animation{
          padding-top: 6vh;
      }
  }